import React,{useState, useEffect} from 'react';
import axios from 'axios';
import { Card, Grid, Typography } from '@material-ui/core';
import useStyles from './useStyles';
import { Card as CardLibrary, CardWrapper } from 'react-swipeable-cards';
import CardQuote from './Card';

const Quotes = () => {
    const [quote, setQuotes] = useState(null)
    const { root, cardStyles } = useStyles();

    useEffect(async () => {
        const response = await axios.get('https://api.quotable.io/random')
        setQuotes(response.data)
    }, [])

    const clientDrag = (e) => {
        if(e.clientX === 0) {
            window.location.reload();
        }
    }

    const initCard = () => {
        return (
            <CardQuote quote={quote} />
        )
    }

    return (
        <div className={root}>
            {<CardQuote /> ? initCard() : initCard() }
        </div>
    )
}


export default Quotes;